
  
  .blog-post-container {
    padding: 2rem;           /* Adds more space inside the individual blog post */
    margin: 0 auto;          /* Centers the post content */
    max-width: 1200px;        /* Limit width of individual posts */
  }
  
  h1 {
    margin-bottom: 1rem;     /* Adds space below the title */
  }
  
  .blog-date {
    margin-bottom: 1.5rem;   /* Adds space below the date */
  }